"use client";

import { useState, useEffect, useRef } from "react";
import Link from "next/link";
import ThemeToggle from "../shared/ThemeToggle";

interface User {
    email?: string;
    firstName?: string;
}

interface HeaderProps {
    user?: User | null;
    onSignIn?: () => void;
    onSignOut?: () => void;
}

export default function Header({ user, onSignIn, onSignOut }: HeaderProps) {
    const [isDropdownOpen, setIsDropdownOpen] = useState(false);
    const dropdownRef = useRef<HTMLDivElement>(null);

    // Close dropdown when clicking outside
    useEffect(() => {
        function handleClickOutside(event: MouseEvent) {
            if (dropdownRef.current && !dropdownRef.current.contains(event.target as Node)) {
                setIsDropdownOpen(false);
            }
        }
        document.addEventListener("mousedown", handleClickOutside);
        return () => document.removeEventListener("mousedown", handleClickOutside);
    }, []);

    const userInitial = user?.firstName?.[0] || user?.email?.[0]?.toUpperCase() || "?";

    return (
        <header className="top-bar">
            <div className="brand">
                <Link href="/" className="brand-wordmark">Recruiter in Your Pocket</Link>
            </div>
            <div className="header-actions">
                {!user ? (
                    <button className="header-btn sign-in-btn" onClick={onSignIn}>
                        Sign In
                    </button>
                ) : (
                    <div className="user-dropdown" ref={dropdownRef}>
                        <button
                            className="user-dropdown-btn"
                            type="button"
                            onClick={() => setIsDropdownOpen(!isDropdownOpen)}
                        >
                            <span className="user-avatar">{userInitial}</span>
                            <span className="user-email-display">{user.firstName || user.email}</span>
                            <span className="dropdown-arrow">‚ñæ</span>
                        </button>
                        {isDropdownOpen && (
                            <div className="user-dropdown-menu">
                                <div className="dropdown-item dropdown-email">{user.email}</div>
                                <div className="dropdown-divider" />
                                <Link href="/workspace" className="dropdown-item dropdown-link dropdown-workspace">
                                    <svg className="dropdown-icon" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                        <path d="M13 6L19 12M19 12L13 18M19 12H5" stroke="currentColor" strokeWidth="1.5"
                                            strokeLinecap="round" strokeLinejoin="round" />
                                    </svg>
                                    Go to Workspace
                                </Link>
                                <div className="dropdown-divider" />
                                <button className="dropdown-item sign-out-btn" type="button" onClick={onSignOut}>
                                    Sign Out
                                </button>
                            </div>
                        )}
                    </div>
                )}
                <ThemeToggle />
            </div>
        </header>
    );
}
